@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.sidebar {
    width: 100%;


    @media screen and (min-width: 990px) {
        width: 25%;
        margin-bottom: 80px;
    }
}

.fotoservice {
    margin-top: 100px;
    margin-bottom: 80px;
}

.contentWrapper {
    @media screen and (min-width: 990px) {
        display: flex;
    }
}

.content {
    @media screen and (min-width: 990px) {
        margin-left: 20px;
        width: 75%;
    }
}

.map {
    width: 100%;
    height: 300px;

    @media screen and (min-width: 767px) {
        height: 400px;
    }
}

.rewiewShow {
    margin-bottom: 50px;
}

.navigator {
    margin: 30px 0px;
}