@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.header{
    font-size: 24px;
    color: #ff3a84;
    margin-top: 25px;
    margin-bottom: 10px;
    line-height: 1.42857143;
}

.item {
    margin-bottom: 20px;
    display: block;    
    width: 100%;
    @media screen and (min-width: 700px) {
        width: 50%;
    }

}

.itemImage {
    margin-right: 10px;
}

.content {

    a {
        display: flex;
        align-items: center;
        color: black;
        width: max-content;
    }

    display: flex;
    flex-wrap: wrap;
}