
$primary_color: #ff3a84;
$secondary_color: #fceb54;

$backdrop_color: #fffabd;

$text_links_color: #ffffff;
$headers_text_color: #262622;
$secondary_text_color: #81817c;

$header_text_color: #ffffff;

$btn_shodow_one: 2px 3.464px 9px 0px rgba(155, 40, 83, 0.75);
$primary_btn_box_shadow_color: rgba(215, 54, 114, 0.7);
$secondary_btn_box_shadow_color:rgba(141, 144, 59, 0.39);
$primary_btn_text_color: #ffffff;


$help_color: #7e2c89;

$background_color: #fff7fa;

$btn_secondary_color: #ffe655;

$footer_background_color: #626262;

$ui_secondary_background: #fef0f5;
@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.sidebar {
    min-height: 190px;
    padding: 50px;
    max-width: 720px;
    border-radius: 18px;
    margin: 0 auto;
    margin-top: 30px;
    box-shadow: -1.256px 17.956px 18px 0px rgba(215, 54, 114, 0.1);
    background: $header_text_color;
    overflow: hidden;
}

.text {
    text-align: center;
    color: $primary_color;
    font-size: 18px;
    font-family: MuseoSans sans-serif;
    font-weight: 700;
}

.navItemImg {
    background-color: aqua;
}

.icon {
    height: 50px;
    width: 50px;
    margin: 15px auto 5px auto;
    background-color: $primary_color;
}

.link {
    color: black;
    &:hover {
        color: $primary_color;
        .icon {
            background-blend-mode: lighten;
        }
    }
}

.items {
    overflow: hidden;

    @media screen and (min-width: 990px) {
        height: max-content;
    }
}

.sidebarBtn {
    margin-top: 20px;
    text-align: center;

    @media screen and (min-width: 990px) {
        display: none;
    }
}

.navbarItem {
    margin-top: 20px;
    width: 50%;
    display: inline-block;
    text-align: center;
    vertical-align: top;
    cursor: pointer;

    @media screen and (min-width: 767px) {
        width: 25%;
    }

    @media screen and (min-width: 990px) {
        width: 100%;

        &:nth-child(1) {
            margin-top: 0px;
        }
    }
}