@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

.weBetter {
    text-align: center;
    width: 100%;
}

.header {
    font-size: 24px;
    font-family: MuseoSans serif;
    font-weight: 700;
    text-align: center;
}

.item {
    margin: 50px 0;
    padding-right: 15px;
    padding-left: 15px;

    @media screen and (min-width: 767px) {
        width: 25%;
        display: inline-block;
    }

    @media screen and (min-width: 1200px) {
        width: 16%;
        display: inline-block;
    }
}

.desc {

    font-size: 16px;
}