
$primary_color: #ff3a84;
$secondary_color: #fceb54;

$backdrop_color: #fffabd;

$text_links_color: #ffffff;
$headers_text_color: #262622;
$secondary_text_color: #81817c;

$header_text_color: #ffffff;

$btn_shodow_one: 2px 3.464px 9px 0px rgba(155, 40, 83, 0.75);
$primary_btn_box_shadow_color: rgba(215, 54, 114, 0.7);
$secondary_btn_box_shadow_color:rgba(141, 144, 59, 0.39);
$primary_btn_text_color: #ffffff;


$help_color: #7e2c89;

$background_color: #fff7fa;

$btn_secondary_color: #ffe655;

$footer_background_color: #626262;

$ui_secondary_background: #fef0f5;
@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.content {
    width: 100%;
}

.rewievBlock {
    border: 2px dotted $primary_color;
    padding: 20px;
    box-shadow: 2px 3.464px 9px 0px rgba(215, 54, 114, 0.39);
    border-radius: 25px;
    margin-bottom: 30px;
    min-height: 182px;
    background-color: #fff;
}

.itemContent {
    padding-top: 20px;
    text-align: center;
}

.header {
    text-align: center;
    font-size: 24px;
    line-height: 1.42;
    margin: 30px 0px;
}

.itemHeader {
    font-size: 16px;
    color: #262622;
    margin-bottom: 10px;
}

.itemText {
    font-family: "MuseoSansRegular";
    font-size: 15px;
    color: #262622;
    text-align: center;

    @media screen and (min-width: 900px) {
        text-align: left;
    }
}

.readMore {
    font-family: "MuseoSansRegular";
    font-size: 15px;
    color: #eb246f;
    text-decoration: underline;
    cursor: pointer;

    &:hover {
        text-decoration: none;
    }
}

.content {
    @media screen and (min-width: 767px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 20px;
    }
}

.rewievBlock {
    margin: 30px 0px;
    width: 100%;


    @media screen and (min-width: 767px) {
        margin: 0px;
    }

    @media screen and (min-width: 900px) {
        display: flex;
    }
}

.avatar {
    height: 128px;
    text-align: center;
    img {
        height: 100%;
    }
}

.showAll {
    text-align: center;
    margin-top: 60px;

    a {
        font-family: "MuseoSansRegular";
        font-size: 15px;
        color: #eb246f;
        text-decoration: underline;
        display: block;
        margin-top: 35px;
        cursor: pointer;

        &:hover {
            text-decoration: none;
        }
    }
}