
$primary_color: #ff3a84;
$secondary_color: #fceb54;

$backdrop_color: #fffabd;

$text_links_color: #ffffff;
$headers_text_color: #262622;
$secondary_text_color: #81817c;

$header_text_color: #ffffff;

$btn_shodow_one: 2px 3.464px 9px 0px rgba(155, 40, 83, 0.75);
$primary_btn_box_shadow_color: rgba(215, 54, 114, 0.7);
$secondary_btn_box_shadow_color:rgba(141, 144, 59, 0.39);
$primary_btn_text_color: #ffffff;


$help_color: #7e2c89;

$background_color: #fff7fa;

$btn_secondary_color: #ffe655;

$footer_background_color: #626262;

$ui_secondary_background: #fef0f5;
@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.header {
    height: 212px;
    overflow: hidden;
    text-align: center;
    margin-bottom: -40px;
    position: relative;
    z-index: 10;
    @media screen and (min-width: 767px) {
        height: 310px;
    }
}

.bg {
    height: 100%;
    width: 100%;
    object-fit: cover;

    @media screen and (min-width: 767px) {
        height: 115%;
        width: 100%;
        object-fit: none;
        object-position: center;
    }
}

.contactBlock {
    background-color: $secondary_color;
    font-size: 15px;
    position: relative;
}

.phonesIcon {
    display: inline-block;
    height: 16px;
    width: 16px;
    margin-right: 10px;

    img {
        height: 100%;
        vertical-align: bottom;
        width: 100%;
    }
}

.contactAddress {
    color: $help_color;
    font-weight: 700;
    text-align: center;
    padding: 10px;
    display: none;
    padding-top: 50px;

    @media screen and (min-width: 767px) {
        display: block;
    }


    @media screen and (min-width: 991px) {
        padding: 0px;
        margin-right: 21px;
    }
}

.contanctPhones {
    padding-top: 15px;
    text-align: center;

    @media screen and (min-width: 767px) {
        padding-top: 0px;
        margin-right: 21px;
    }
}

.contactMail {
    z-index: 1000;
    text-align: center;
    padding-top: 10px;
    display: none;

    a {
        text-decoration: none;
        color: $help_color;
    }

    @media screen and (min-width: 767px) {
        display: block;
    }

    @media screen and (min-width: 991px) {
        padding-top: 0px;
    }
}

.orderBtn {
    padding-top: 15px;
    padding-bottom: 25px;
    display: flex;
    justify-content: center;

    @media screen and (min-width: 991px) {
        display: none;
    }

    @media screen and (min-width: 1235px) {
        display: block;
        position: absolute;
        z-index: 1000;
        top: -60px;
        right: -30px;
    }
}

.br {

    @media screen and (min-width: 767px) {
        display: none;
    }
}

.phones {
    position: relative;
    z-index: 1000;
    a {
        text-decoration: none;
        color: $help_color;
    }
}

.mailIcon {
    display: inline-block;
    height: 16px;
    width: 16px;
    margin-right: 10px;

    img {
        height: 100%;
        width: 100%;
        vertical-align: bottom;
    }
}

.wrapper {
    font-family: MuseoSans sans-serif;
    font-weight: 500;

    @media screen and (min-width: 991px) {
        display: flex;
        height: 40px;
        margin-top: 14px;
        justify-content: center;
        align-items: center;
        max-width: 1097px;
        position: relative;
    }
}

