
$primary_color: #ff3a84;
$secondary_color: #fceb54;

$backdrop_color: #fffabd;

$text_links_color: #ffffff;
$headers_text_color: #262622;
$secondary_text_color: #81817c;

$header_text_color: #ffffff;

$btn_shodow_one: 2px 3.464px 9px 0px rgba(155, 40, 83, 0.75);
$primary_btn_box_shadow_color: rgba(215, 54, 114, 0.7);
$secondary_btn_box_shadow_color:rgba(141, 144, 59, 0.39);
$primary_btn_text_color: #ffffff;


$help_color: #7e2c89;

$background_color: #fff7fa;

$btn_secondary_color: #ffe655;

$footer_background_color: #626262;

$ui_secondary_background: #fef0f5;
@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.imageWrapper {
    height: 100%;
    width: 100%;
    position: relative;
}

.sliderImg {
    width: 100%;
}

.imageFilter {
    position: absolute;
    top: 0;
    left: 0;
    height: calc(100% - 2px);
    width: 100%;
    background: #000;
    opacity: 0.6;
}

.slideContent {
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 100;
    color: $header_text_color;
    padding-left: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.slideHeader {
    font-size: 30px;
    text-transform: uppercase;
    color: $header_text_color;
    font-family: MouseoSans sans-serif;
    font-weight: bold;
    margin-bottom: 20px;
}

.slideSubTitle {
    font-size: 20px;
    margin-bottom: 40px;
    color: $header_text_color;
    font-family: MouseoSans sans-serif;
}

.slideBtnText {
    font-size: 16px;
    font-size: MouseSans sans-serif;
    font-weight: 300;
}

.slider {
    position: relative;
    
    @media screen and (max-width: 990px) {
        display: none;
    }
}

.sliderBtn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 20px;
    width: 20px;
    z-index: 100;
    cursor: pointer;
}

.sliderPrevBtn {
    left: 20px;
}

.sliderNextBtn {
    right: 40px;
}