
$primary_color: #ff3a84;
$secondary_color: #fceb54;

$backdrop_color: #fffabd;

$text_links_color: #ffffff;
$headers_text_color: #262622;
$secondary_text_color: #81817c;

$header_text_color: #ffffff;

$btn_shodow_one: 2px 3.464px 9px 0px rgba(155, 40, 83, 0.75);
$primary_btn_box_shadow_color: rgba(215, 54, 114, 0.7);
$secondary_btn_box_shadow_color:rgba(141, 144, 59, 0.39);
$primary_btn_text_color: #ffffff;


$help_color: #7e2c89;

$background_color: #fff7fa;

$btn_secondary_color: #ffe655;

$footer_background_color: #626262;

$ui_secondary_background: #fef0f5;
@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.menu {
    background-color: $primary_color;
}

.wrapperDesktop {
    height: 50px;
    display: none;

    @media screen and (min-width: 767px) {
        display: block;
    }

    .ulistDesk {
        height: 100%;
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: center;
        align-items: center;

        :nth-child(1)::after {
            display: none;
        }

        li {
            &:hover {
                color: #f3e6ab;
            }
        }

        .menuElemDesk {
            margin-right: 15px;
            font-family: MouseoSans sans-serif;
            position: relative;
            padding-left: 15px;

            &::after {
                content: "";
                position: absolute;
                width: 1px;
                height: 100%;
                left: 0;
                top: 0;
                background-color: #ff6ea5;
            }

            
        }
    }
}

.wraperMobile {

    @media screen and (min-width: 767px) {
        display: none;
    }
}

.innerMobile {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 40px;
}

.menuText {
    font-family: MouseoSans sans-serif;
    font-weight: 700;
    font-size: 18px;
    color: $primary_btn_text_color;
}

.list {
    transition: all 300ms;
    overflow: hidden;
}

.active {
    height: max-content;
}

.ulist {
    padding: 0;
    padding-left: 40px;
    padding-bottom: 10px;
    margin: 0;
    color: $primary_btn_text_color;
}

.menuElem {
    margin-bottom: 15px;
}

.active {
    color: #f3e6ab;
    cursor: default;
}

.enactive {
    color: $primary_btn_text_color;
    cursor: pointer;
}