
$primary_color: #ff3a84;
$secondary_color: #fceb54;

$backdrop_color: #fffabd;

$text_links_color: #ffffff;
$headers_text_color: #262622;
$secondary_text_color: #81817c;

$header_text_color: #ffffff;

$btn_shodow_one: 2px 3.464px 9px 0px rgba(155, 40, 83, 0.75);
$primary_btn_box_shadow_color: rgba(215, 54, 114, 0.7);
$secondary_btn_box_shadow_color:rgba(141, 144, 59, 0.39);
$primary_btn_text_color: #ffffff;


$help_color: #7e2c89;

$background_color: #fff7fa;

$btn_secondary_color: #ffe655;

$footer_background_color: #626262;

$ui_secondary_background: #fef0f5;
@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
* {
    box-sizing: border-box;
  }

html, body, input {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    position: relative;
    background: $background_color;
}

ul {
    text-decoration: none;
    list-style: none;
}

a {
    text-decoration: none;
}

.container {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0px 15px;
    height: 100%;
}

.disable-scroll {
    overflow: hidden;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
}

.common_header {
    font-family: MuseoSans sans-serif;
    font-weight: 700;
    margin-top: 25px;
    margin-bottom: 18px;
    color: $primary_color;
}

.common_title {
    color: $primary_color;
    margin-top: 50px;
}

.common_about {
    p {
        font-size: 15px;
        line-height: 1.6em;
        margin: 0 0 10px;
    }
}


.common_imageBlock {
    margin-left: -7.5px;
    img {
        display: inline-block;
        padding-right: 7.5px;
        padding-left: 7.5px;
        max-width: 350px;
        width: 33%;
    }
}

.common_video {
    height: 230px;
    max-width: 400px;
    margin: 0 auto;
}

.common_list {

    ul {
        list-style: circle;
    }

    li {
        font-family: inherit;
        margin: 5px 0;
        font-size: 15px;
        line-height: 1.6em;
    }
}

.common_link {
    font-family: "MuseoSansBold";
    color: #ff3a84;
    text-decoration: underline;
}
.std_table {
    td {
        padding: 5px 10px;
        border: 1px solid;
        font-size: 15px;
    }
}

.bold_text {
    font-weight: bold;
}