
$primary_color: #ff3a84;
$secondary_color: #fceb54;

$backdrop_color: #fffabd;

$text_links_color: #ffffff;
$headers_text_color: #262622;
$secondary_text_color: #81817c;

$header_text_color: #ffffff;

$btn_shodow_one: 2px 3.464px 9px 0px rgba(155, 40, 83, 0.75);
$primary_btn_box_shadow_color: rgba(215, 54, 114, 0.7);
$secondary_btn_box_shadow_color:rgba(141, 144, 59, 0.39);
$primary_btn_text_color: #ffffff;


$help_color: #7e2c89;

$background_color: #fff7fa;

$btn_secondary_color: #ffe655;

$footer_background_color: #626262;

$ui_secondary_background: #fef0f5;
@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.header {
    color: $primary_color;
    font-size: 20px;
}

.inputs {
    display: flex;
    justify-content: space-between;


    @media screen and (max-width: 700px) {
        flex-direction: column;
    }

    div {
        width: 47%;

        @media screen and (max-width: 700px) {
            width: 100%;
            &:nth-child(2) {
                margin-top: 20px;
            }
        }
    }
}

.area {
    margin-top: 30px;
}

.captcha {
    margin: 20px 0;
}

.subheader {
    margin-top: 60px;
}

.reviev_header {
    font-size: 16px;
    color: #262622;
    margin-bottom: 10px;
    font-weight: 700;
}

.rewievBlock {
    padding: 20px;
    border-radius: 25px;
    margin-bottom: 30px;
    min-height: 182px;
}

.itemContent {
    padding-top: 20px;
    text-align: center;
}



.itemHeader {
    font-size: 16px;
    color: #262622;
    margin-bottom: 10px;
    font-weight: 700;
    margin-bottom: 20px;

    @media screen and (min-width: 900px) {
        text-align: left;
    }
}

.itemText {
    font-family: "MuseoSansRegular";
    font-size: 15px;
    color: #262622;
    text-align: center;

    @media screen and (min-width: 900px) {
        text-align: left;
    }
}

.readMore {
    font-family: "MuseoSansRegular";
    font-size: 15px;
    color: #eb246f;
    text-decoration: underline;
    cursor: pointer;
    white-space: nowrap;

    &:hover {
        text-decoration: none;
    }
}


.rewievBlock {
    margin: 30px 0px;
    width: 100%;


    @media screen and (min-width: 767px) {
        margin: 0px;
    }

    @media screen and (min-width: 900px) {
        display: flex;
    }
}

.avatar {
    height: 128px;
    text-align: center;
    margin-right: 30px;
    img {
        border-radius: 50%;
        height: 100%;
    }
}

.showAll {
    text-align: center;
    margin-top: 60px;

    a {
        font-family: "MuseoSansRegular";
        font-size: 15px;
        color: #eb246f;
        text-decoration: underline;
        display: block;
        margin-top: 35px;
        cursor: pointer;

        &:hover {
            text-decoration: none;
        }
    }
}