
$primary_color: #ff3a84;
$secondary_color: #fceb54;

$backdrop_color: #fffabd;

$text_links_color: #ffffff;
$headers_text_color: #262622;
$secondary_text_color: #81817c;

$header_text_color: #ffffff;

$btn_shodow_one: 2px 3.464px 9px 0px rgba(155, 40, 83, 0.75);
$primary_btn_box_shadow_color: rgba(215, 54, 114, 0.7);
$secondary_btn_box_shadow_color:rgba(141, 144, 59, 0.39);
$primary_btn_text_color: #ffffff;


$help_color: #7e2c89;

$background_color: #fff7fa;

$btn_secondary_color: #ffe655;

$footer_background_color: #626262;

$ui_secondary_background: #fef0f5;
@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.overlay {
    height: 100%;
    width: 100%;
    background-color: $backdrop_color;
    display: flex;
    justify-content: center;
    align-items: start;
    cursor: pointer;
}

.modal {
    opacity: 0;
    transition: all 300ms;
    z-index: 1000;
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
}

.showModal {
    opacity: 1;
    transition: 300ms;
}

.content {
    width: 80%;
    padding:0px 30px;
    background-color: $backdrop_color;
    position: relative;
    top: 30px;
    cursor: default;


    @media screen and (min-width: 767px) {
        width: 476px;
        top: 200px;
    }
}

.modalHeader {
    text-align: center;
    font-size: 24px;
    padding: 0px;
    margin: 0px;
    line-height: 1.42857143;
}

.closer {
    font-size: 22px;
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 0;
}

.modalBody {
    width: 100%;

}

.description {
    margin: 0 0 10px;
    font-weight: 300;
    display: block;
    text-align: center;
    font-size: 14px;
}

.submitBtn {
    text-align: center;
}

.submitText {
    text-align: center;
}