@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/medium/MuseoSans500.woff) format(woff),
         url(/fonts/museoSans/medium/MuseoSans500.ttf) format(ttf);
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/bold/MuseoSans700.woff) format(woff),
         url(/fonts/museoSans/bold/MuseoSans700.ttf) format(ttf);
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}

@font-face {
    font-family: MuseoSans;
    src: url(/fonts/museoSans/light/MuseoSans.woff/) format(woff),
         url(/fonts/museoSans/light/MuseoSans.ttf/) format(ttf);
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    font-variant: normal;
}
.burger {
    cursor: pointer;
    height: 17px;
    width: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    span {
        display: block;
        height: 2px;
        width: 100%;
        background-color: $text_links_color;
        transition: all 100ms linear;
    }
}

.active {

    :nth-child(1) {
        transform: translate(0, 7px) rotate(45deg);
    }

    :nth-child(2) {
        display: none;
    }

    :nth-child(3) {
        transform: translate(0, -8px) rotate(-45deg);
    }
}